	background-repeat: repeat;
}

.HomePage__container {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
}

.HomePage__introduce,
.HomePage-logo {
	width: 80%;
	max-width: 500px;
	color: #ffff;
	text-align: center;
	padding-top: 25px;
}

.HomePage-logo {
