}

@media screen and (min-width: 800px) {
	.HomePage__introduce,
	.HomePage-logo {
		padding-top: 0;
	}

	.HomePage__container {
		padding: 15% 0;
		-webkit-flex-direction: row;
		        flex-direction: row;
		-webkit-justify-content: space-between;
		        justify-content: space-between;
	}
}

@media screen and (min-width: 1000px) {
	.HomePage-logo {
