	}

	.HomePage__container {
		padding-top: 9%;
	}
}

.Badge-hero {
	width: 100%;
	padding: 30px 0;
	background: url(/platzi-badges/static/media/stars.1d17360a.svg), #1b1b25;
	background-repeat: repeat;
}

.Badges__link {
	width: 100%;
