.BadgesList {
	padding: 0;
}

.BadgeList__item {
	list-style: none;
	background: #ffff;
	border-radius: 6px;
	margin: 15px auto;
	overflow: hidden;

	border: 1px solid rgb(45, 196, 255);
}

.BadgeList__header {
	min-height: 30px;
	padding: 0 20px;
	background: rgb(236, 236, 236);
	border-bottom: 1px solid rgb(216, 216, 216);

	display: -webkit-flex;

	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
}

.BadgeList__data {
	padding: 20px 0px;

	display: -webkit-flex;

	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
}

.Badge__data-avatar {
	width: 100px;
	border-radius: 50%;
}

.Badge__data-description {
	margin-top: 20px;

	display: -webkit-flex;

	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: flex-start;
	        align-items: flex-start;
	-webkit-justify-content: center;
	        justify-content: center;
}

.Badge__data-description > p {
	width: 100%;
	display: block;
	text-align: center;
}

.BadgesList__link {
	color: rgb(3, 146, 241);
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}

.BadgesList__link :hover {
	color: rgb(0, 127, 212);
}

.Badge__data-description > a,
.Badge__data-description > p {
	font-size: 1.6rem;
}

@media screen and (min-width: 730px) {
	.BadgeList__data {
		padding: 20px 25px;
		-webkit-flex-direction: row;
		        flex-direction: row;
		-webkit-justify-content: space-around;
		        justify-content: space-around;
	}

	.Badge__data-description {
		margin-top: 0px;
	}

	.Badge__data-description > p {
		text-align: start;
	}
}

.PageLoading {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	width: 100%;
}

@-webkit-keyframes Loader {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes Loader {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

.Loader {
	width: 50px;
	height: 50px;
	border-radius: 50%;
	border: 5px solid #d8e2d5;
	border-top: 5px solid #172e42;

	-webkit-animation: Loader 2s ease infinite;

	        animation: Loader 2s ease infinite;
}

.BadgeNew__hero {
	width: 100%;
	padding: 40px 0px;
	background: url(/platzi-badges/static/media/stars.1d17360a.svg), #1b1b25;
	background-repeat: repeat;
}

.Badge-logo {
	display: block;
	width: 210px;
	margin: 0 auto;
}

.BadgeNew__container {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
